.NavItems {
  list-style: none;
  padding: 0 1rem;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex: 1;
}

@media only screen and (max-width: 50em) {
  .NavItems {
    flex-direction: column;
    justify-content: start;
  }

  .NavItems > * {
    margin-bottom: 8rem;
  }
}
